#support-card {
	max-width: 45vw;
	max-height: 90vh;
	min-width: 45vw;
	min-height: 90vh;
}

#Form-Header-Info,
#Form-Header-Request {
    font-size: 25px;
    color:#626988;
    max-width: 95%;
    min-width: 95%;
    width: 95% !important;
    padding: 0;
}
#Form-Header-Request>div {
	font-weight: 500;
	text-indent: 20px;
}

.form-item-request-cont {
	display: grid;
	grid-template-columns: repeat(2, 47.5%);
	align-content: center;
	justify-content: center;
	gap: 2.5%;
	row-gap: 20px;
}

.form-item-request-cont>.form-info-item {
	display: flex;
	flex-direction: column-reverse;
	justify-content: start;
}
.form-item-request-cont>.form-info-item>.form-info-title,
.problem-descr-hint {
	font-style: italic;
	color: #424242;
	text-align: left;
}
.problem-descr-hint {
	padding: 10px;
}

.form-item-information-cont>.form-info-item {
	display: grid;
	grid-template-columns: 20% 75%;
	gap: 5%;
	width: 100%;
	padding: 20px;
	align-content: center;
	justify-content: center;
}
.form-item-information-cont>.form-info-item>.form-info-title {
	width: 100%;
	height: 100%;
	font-size: 25px;
}
.form-item-information-cont>.form-info-item>input,
.form-item-request-cont>.form-info-item>input {
	width: 100%;
	height: 40px;
}

.form-item-request-cont>.form-info-item>.dropdown-cont>.dropdown-option.selected {
	height: 40px;
	padding: 10px;
}

#Form-Header-Info-cont,
#Form-Header-Request {
	display: flex;
}

.form-footer {
	width: calc(100% - 20px);
}
.form-footer>.action-button {
	height: 40px;
	font-size: 25px;
	line-height: 40px;
	background-color: #ffcf90;
	color:#111111;
	font-weight: 550;
}
.form-footer>.action-button:hover {
	background-color: #eebc7b;
}
#submit-button {
	background-color: #b6eeff;
}
#submit-button:hover {
	background-color: #98d3e6;
}

#problem-information-form {
	display: flex;
	flex-direction: column;
}
#problem-information-form>.form-info-item {
	width: 100%;
}
#problem-information-form>.form-info-item>textarea {
	border: 1px solid #ccc;
	min-width: 100%;
	max-width: 100%;
	min-height: 200px;
	max-height: 200px;
	padding: 10px;
	font-size: 20px;
}
#problem-information-form>.form-info-item>textarea:focus {
	background-color: #ccc;
}

#category-description-cont {
	display: grid;
	grid-template-columns: repeat(2, 50%);
}

#category-description-cont>.form-info-item {
	padding: 10px;
	display: grid;
	grid-template-columns: 30% 65%;
	gap: 5%;
	justify-content: center;
}
#category-description-cont>.form-info-item>.form-info-title {
	font-size: 1.25rem;
	height: 40px;
	line-height: 35px;
}
#category-description-cont>.form-info-item>input {
	height: 40px;
	width: 100%;
}
#support-card-card-content {
	height: calc(100% - 40px);
	overflow-y: overlay;
}

/*Flip multi-select dropdown to go up*/
#account-selection-cont>.multi-select-cont {
	display: flex;
	flex-direction: column-reverse;
}
#account-selection-cont>.multi-select-cont>.multi-select-list {
	position: relative;
	border-top-right-radius: 5px;
	border-top-left-radius: 5px;
}
#account-selection-cont>.multi-select-cont>.multi-select-list {
	border-bottom-left-radius: 0px;
	border-bottom-right-radius: 0px;
}
#account-selection-cont>.multi-select-cont.true {
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
	border-top-right-radius: 0px;
	border-top-left-radius: 0px;
}
#account-selection-cont>.multi-select-cont>.multi-select-selector.true {
	line-height: 50px;
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
	border-top-right-radius: 0px;
	border-top-left-radius: 0px;
	z-index: 50;
}
#account-selection-cont>.multi-select-cont>.multi-select-selector>.multi-select-pill-list::-webkit-scrollbar {
	height: 8px !important;
}
#account-selection-cont>.multi-select-cont>.multi-select-selector>.multi-select-pill-list {
	align-items: center;
	justify-items: center;
	display: flex;
	flex-direction: row;
	overflow-x: overlay;
	overflow-y: hidden !important;
	max-width: 97.5%;
}
#account-selection-cont>.multi-select-cont>.multi-select-selector>.multi-select-pill-list>.multi-select-pill {
	line-height: 100%;
	text-align: center;
	align-content: center;
	
	vertical-align: middle;
	margin-top: 5px;
	margin-bottom: 5px;
	height: 75%;
	min-width: fit-content;
}

/*Program problem form customization*/
#program-problem-description-cont {
	width: 200%;
	display: flex;
	flex-direction: column;
	gap: 10px;
}
#program-problem-description-cont>.form-info-item {
	display: grid;
	grid-template-columns: 37.5% 60%;
	height: 50px;
	align-items: center;
	justify-items: center;
}
#program-problem-description-cont>.form-info-item>.form-info-title {
	font-size: 25px;
	text-align: left;
}
#program-problem-description-cont>.form-info-item>.dropdown-cont>.dropdown-option.selected {
	height: 40px;
	font-size: 20px;
	line-height: 30px;
}
#program-problem-description-cont>.form-info-item>input {
	height: 40px;
	width: 100%;
}

.dropdown-list {
	min-height: fit-content;
	max-height: 175px;
}

#print-logo {
	display: none;
}
/*Free type input list CSS*/
#category-description-cont>.form-info-item>.list-input-cont {
	width: 100%;
}
#category-description-cont>.form-info-item>.list-input-cont>.list-input-actions {
	display: grid;
	width: 100%;
	grid-template-columns: 75% 25%;
}
#category-description-cont>.form-info-item>.list-input-cont>.list-input-actions>input {
	width: 100%;
	height: 40px;
}

/*tableting: landscape*/
@media (orientation:landscape) and (max-width: 1400px) {
	#support-card {
		min-width: 70vw;
		max-width: 70vw;
	}
	#support-card-card-content {
		height: calc(100% - 40px);
		overflow-y: overlay;
	}
}

/*tableting: landscape*/
@media (orientation:portrait) and (max-width: 1400px) {
	#support-card {
		min-width: 90vw;
		max-width: 90vw;
	}
	#support-card-card-content {
		height: calc(100% - 40px);
		overflow-y: overlay;
	}
}

/*Print layout*/
@media print {
	#print-logo {
		display: block;
		height: 225px;
	}
	/*Fix the float containers to stack*/
	.float-container.overlay {
		position: relative;
		padding: 0;
		margin: 0;
		height: unset;
		background-color: #fff;
	}
	body {
		background-color: #fff;
	}
	/*Widen cards and hide menubars/shadows*/
	#support-card {
		filter: unset;
		min-width: 7.5in;
		max-width: 7.5in;
		padding: 0;
		margin: 0;
		min-height: fit-content;
		max-height: fit-content;
		overflow-y:visible;
		page-break-after: always;
	}
	#support-card>.card-menubar {
		display: none;
	}
	#support-card>.card-content {
		height: fit-content;
		border: 2px solid black;
		border-radius: 5px;
	}
	

	/*Hide all action buttons*/
	.action-button {
		display: none;
	}

	/*Adjust the styling of inputs*/
	.checkbox-cont,
	input,
	.dropdown-cont>.dropdown-option.selected,
	.multi-select-cont {
		border: 0px;
		border-radius: 0px;
		border-bottom: 2px solid black;
		outline: 0px;
		font-size: 15px;
	}
	.checkbox-check {
		background-color: transparent;
		filter: unset;
	}
	textarea {
		border: 2px solid black !important;
		border-radius: 0px;
		overflow: hidden;
		min-height: fit-content !important;
		max-height: fit-content !important;
	}
	/*Adjust the grid of the contact info form*/
	.form-item-information-cont,
	.form-item-request-cont,
	#category-description-cont {
		display: grid;
		grid-template-columns: repeat(2, 50%);
		padding: 0px;
		margin: 0px;
		gap: 0;
		row-gap:0;
	}

	/*Adjust the sizing of form items*/
	.form-item-information-cont>.form-info-item,
	.form-item-request-cont>.form-info-item {
		display: flex;
		flex-direction: column-reverse;
		padding: 5px;
	}
	.form-item-information-cont>.form-info-item>.form-info-title,
	.form-item-request-cont>.form-info-item>.form-info-title {
		font-size: 18.5px;
	}
	#category-description-cont>.form-info-item {
		padding: 5px;
		margin: 5px;
	}
	.form-info-title {
		color: #000 !important;
		font-style: normal !important;
	}
	.form-info-item {
		border: 2px solid black;
		display: grid !important;
		grid-template-columns: 50% 50%;
		padding: 5px !important;
	}
	/*Hide dropdowns*/
	.dropdown-arrow {
		display: none;
	}

	/*Customize the multi-selects*/
	.multi-select-cont {
		flex-direction: column;
		height: fit-content;
		border: 0px;
	}
	.multi-select-cont>.multi-select-selector {
		height: fit-content;
		border: 0px;
	}
	.multi-select-pill-list {
		overflow-y: visible;
		overflow-x: unset;
		display: flex;
		flex-direction: column !important;
		height: fit-content;
		align-items: unset !important;
		justify-items: unset !important;
	}
	.multi-select-pill {
		text-align: left !important;
		border-radius: 0px;
		border: 0px;
		border-bottom: 2px solid black;
		background-color: transparent;
	}

	/*Adjust header font size*/
	#Form-Header-Info>div, #Form-Header-Request>div {
		font-size: 30px;
	}
	.login-cont, .loadscreen {
		display: none;
	}

	/*Widen the last item in the contact info*/
	.form-item-information-cont>:nth-child(5) {
		width: 200%;
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
		border-top: 0;
	}

	/*Adjust the borders*/
	.form-item-information-cont>:nth-child(2),
	.form-item-information-cont>:nth-child(4),
	.form-item-request-cont>:nth-child(2),
	.form-item-request-cont>:nth-child(4),
	.form-item-request-cont>:nth-child(6) {
		border-left: 0px;
	}
	.form-item-information-cont>:nth-child(1),
	.form-item-information-cont>:nth-child(2),
	.form-item-request-cont>:nth-child(1),
	.form-item-request-cont>:nth-child(2),
	.form-item-request-cont>:nth-child(3),
	.form-item-request-cont>:nth-child(4) {
		border-bottom: 0px;
	}
	.form-item-information-cont>:nth-child(1),
	.form-item-request-cont>:nth-child(1) {
		border-top-left-radius: 5px;
	}
	.form-item-information-cont>:nth-child(2),
	.form-item-request-cont>:nth-child(2) {
		border-top-right-radius: 5px;
	}
	.form-item-request-cont>:nth-child(5) {
		border-bottom-left-radius: 5px;
	}
	.form-item-request-cont>:nth-child(6) {
		border-bottom-right-radius: 5px;
	}

	/*Hide the description title*/
	#problem-information-form>:nth-child(2)>.form-info-title {
		display: none;
	}

	/*Adjust the problem info form*/
	#problem-information-form {
		padding: 0px;
		margin: 0px;
	}
	#problem-information-form>:nth-child(1) {
		border-top-right-radius: 5px;
		grid-template-columns: 25% 75%;
	}
	#problem-information-form>:nth-child(2) {
		padding: 0px !important;
		margin: 0px !important;
		border: 0px !important;
		grid-template-columns: 100% 0;
	}
	#problem-information-form>:nth-child(3) {
		border: 0px;
	}
	#problem-information-form>:nth-child(3)>.form-info-title {
		display: none;
	}
	.text-area {
		border-radius: 5px;
	}

	#program-problem-description-cont>.form-info-item {
		border-radius: 5px;
	}
	.header-text {
		text-indent: 0px !important;
		font-size: 35px !important;
	}

	#support-card-card-content {
		display: flex;
		flex-direction: column-reverse;
	}
}